@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;300;400&display=swap');


@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-family: 'Noto Sans',sans-serif;
}

.card{
    @apply bg-blue-600  flex-1 rounded-lg relative overflow-hidden  
}
.card::before{
    content: '';
    @apply bg-indigo-600 h-72 w-1/2 rounded-r-full absolute
}
.card::after{
    content: '';
    @apply bg-cyan-300 h-32 w-32 -right-9 -top-9 rounded-full absolute
}
.content-left::-webkit-scrollbar{
    display: none;

}